import Vue from 'vue'
import Vuex from 'vuex'//vuex

Vue.use(Vuex)

// 
export default new Vuex.Store({
  state: {
    phone: "",//
    id: ""
  },
  mutations: {
    setphone(state, row) {
      state.phone = row.phone;
    },
    setid(state, row) {
      state.id = row.id;
    }
  },
  actions: {

  }
})
