import Vue from 'vue'

import {
    // 
    Search,
    // 
    Swipe,
    SwipeItem,
    Lazyload,//
    // 
    Grid,
    GridItem,
    Image,
    // 
    Tabbar,
    TabbarItem,
    // tab
    Tab, Tabs,
    // 
    Card,
    // 
    CountDown,
    // 
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton,
    // 
    Field,
    CellGroup,
    Cell

} from 'vant';

export default () => {
    Vue.use(Search);
    Vue.use(Swipe).use(SwipeItem);
    Vue.use(Grid).use(GridItem);
    Vue.use(Lazyload);
    Vue.use(Image);
    Vue.use(Tabbar).use(TabbarItem);
    Vue.use(Tab).use(Tabs);
    Vue.use(Card);
    Vue.use(CountDown);
    Vue.use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton);
    Vue.use(Field);
    Vue.use(Cell).use(CellGroup);
}