<template>
  <div id="login_bk">
    <div class="Login">
      <div class="logo">
        <img :src="login" />
      </div>
      <p class="title t_gray1">
        
        <!-- -->
        
      </p>
      <div class="InpBox InputBarWrap" style="margin-bottom:16px">
        <!--  -->
        <van-cell-group>
          <van-field
            v-model="phone"
            label=""
            placeholder=""
            :error-message="message"
            @input="regphone"
          />
          <van-field v-model="pwd" label="" placeholder="" />
        </van-cell-group>
      </div>
      <p class="tiptStr">
        <strong></strong>
        <a
          href="https://m.vsigo.cn/h5/LoginProtocol.html"
          class="t_red"
        ></a>
      </p>
      <div class="btnWrap">
        <button class="defaultBtn" type="button" @click="zhuce"></button>
        <button type="button" data-sort="phone" @click="denglu"></button>
      </div>
      <div class="footerWrap" style="display: block;">
        <ul class="fl-row-around">
          <li style="text-align:center">
            <a href="javascript:;" target="_self">
              <img :src="qq" style="width:50px" />
            </a>
          </li>
          <li style="text-align:center">
            <a target="_self">
              <img :src="weibo" style="width:50px" />
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/css/login.css";
import login from "../assets/imgs/logoNew.png";
import qq from "../assets/imgs/qqIcon.png";
import weibo from "../assets/imgs/weiboIcon.png";

export default {
  data() {
    return {
      login, //
      qq, //
      weibo, //
      phone: "", //
      pwd: "", //
      message: "", //
      phoneok: "" //
    };
  },
  methods: {
    // 
    // ok
    async denglu() {
      if (this.phoneok) {
        let res = await this.$axios.post("http://localhost:3000/login/denglu", {
          phone: this.phone,
          pwd: this.pwd
        });
        // console.log(res)
        let row = { phone: this.phone }; //
        this.$store.commit("setphone", row); //store
        // this.phone = ""; //
        // this.pwd = "";
        if (res.data == "") {
          this.$router.push("/wode");
        }
      } else {
        this.message = "";
        this.phone = "";
        this.pwd = "";
      }
    },
    // 
    // ok
    async zhuce() {
      await this.$axios.post("http://localhost:3000/login/zhuce", {
        phone: this.phone,
        pwd: this.pwd
      });
    },
    // 
    regphone() {
      let data = this.phone;
      // console.log(data);
      // 
      if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(data)) {
        this.message = "";
        this.phoneok = false;
      } else {
        this.message = "";
        this.phoneok = true;
      }
    }
    // 
  }
};
</script>

<style scoped>
#login_bk {
  background-color: #fff;
}
.InputBarWrap .inputWrap input {
  font-size: 0.3rem;
}
.InputBarWrap .tips {
  width: 0.8rem;
  height: 0.8rem;
  text-align: center;
  line-height: 0.8rem;
}
.InputBarWrap .codeBtn,
.Login .btnWrap button {
  font-size: 0.3rem;
}
.Login .tiptStr {
  font-size: 0.27rem;
}
.fl-row-around {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
}
</style>
<style>
.van-field__label {
  padding-left: 10px;
  width: 60px;
}
.van-cell-group {
  position: relative;
}
.icon-icon_phone {
  position: absolute;
  top: 10px;
  left: 5px;
}
</style>


